<!-- 本文件为最外层组件 -->
<!-- 模板 -->
<template>
  <!-- 模板，用于替换html中的内容（优先级大于el小于render） -->
  <div>
    <div>
      <input v-model="inputValue" />
      <button @click="handlesubmit">submit</button>
      <ul>
        <!-- 用for循环输出相应数据，同时将对应内容和index作为参数传送给外部局域组件，同时监听名为delete（可以自己修改）的事件 -->
        <todo-item @delete="deal_delete" v-for="(i,index) of list" :key="index" :content="i" :index="index"></todo-item>
      </ul>
    </div>
    <p>
      <router-link to="/home">home</router-link>//跳转首页
      <router-link to="/test">test</router-link>//跳转新页面
      </p>

      <router-view></router-view>
  </div>
</template>

<!-- 逻辑 -->
<script>
  //引入外部局域组件
  import todoitem from './components/todoitem'

  export default {
    components: {
      //声明该组件
      'todo-item': todoitem
    },
    data() {
      return {
        inputValue: "",
        list: []
      }
    },
    methods: {
      //cs6的函数写法
      handlesubmit() {
        //this指当前的对象
        this.list.push(this.inputValue)
        this.inputValue = ""
      },
      //一般写法
      deal_delete:function (index) {
        this.list.splice(index, 1)
      }
    }
  }
</script>

<!-- 样式 -->
<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>


