
<template>
    <div>
    <!-- 定义列表器件的点击事件 -->
      <h1>THis is the result by component : Hello</h1>
    </div>
</template>

<script>
    export default {
    //定义接收的参数
props: ['content', 'index'],
methods: {
    handledelete( ) {
    //  alert(this.index)
    //向外发布delete的事件，同时传递index参数
    this.$emit('delete',this.index)
    }
}
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- 添加作用域元素去限制css效果只在该组件中成立 -->
<style scoped>
.item{
    color:rgb(217, 255, 0);
}
</style>
    
    


